body{
    background: -webkit-linear-gradient(left, #0072ff, #00c6ff);
}
.contact-form{
    background: #fff;
    margin-top: 10%;
    margin-bottom: 5%;
    width: 70%;
}
.contact-form .form-control{
    border-radius:1rem;
}
.contact-image{
    text-align: center;
}
.contact-image img{
    border-radius: 6rem;
    width: 11%;
    margin-top: -3%;
    transform: rotate(0deg);
}
.contact-form form{
    padding: 0;
}
.contact-form form .row{
    /* margin-bottom: -7%; */
}
.contact-form h3{
    margin-bottom: 2%;
    margin-top: 0%;
    text-align: center;
    color: #fff;
    border: 2px solid #00c6ff;
    padding: 3px;
    background: #00c6ff;
    border-radius: 8px;
}
.contact-form .btnContact {
    width: 50%;
    border: none;
    border-radius: 1rem;
    padding: 1.5%;
    background: #dc3545;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}
.btnContactSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    color: #fff;
    background-color: #0062cc;
    border: none;
    cursor: pointer;
}

span.form-error-msg {
    color: orangered;
    float: left;
    padding-left: 10px;
}